
import React from 'react';
import P2PBlogReachView from 'views/P2PBlogReachView';
import Main from 'layouts/SP2P';
import WithLayout from 'WithLayout';
import { graphql } from 'gatsby'

const P2PLandingPage = (data) => {
  return (
    <WithLayout
      component={P2PBlogReachView}
      layout={Main}
      data={data}
    />
  )
};

export default P2PLandingPage;

export const pageQuery = graphql`
  query AllBlogPosts {
    allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]}) {
        edges {
            node {
                excerpt(pruneLength: 250)
                id
                frontmatter {
                    title
                    date(formatString: "MMMM DD, YYYY")
                    path
                    subtitle
                    tags
                    cover
                    author
                }
            }
        }
    }
  }`