import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Section, SectionAlternate } from 'components/organisms';
import {
  Hero,
  Horizontal,
} from './components';
import { useStaticQuery, graphql } from 'gatsby'
import { useFlexSearch } from 'react-use-flexsearch'
import * as queryString from 'query-string'

import { simplifyEdges } from 'utils/graphUtils';

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    width: '100%',
  },
  pagePaddingTop: {
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3),
    [theme.breakpoints.up('md')]: {
      paddingTop: theme.spacing(5),
      paddingBottom: theme.spacing(5),
    },
  },
}));

const P2PBlogReachView = ({ data, location, navigate }) => {
  const classes = useStyles();
  const posts = simplifyEdges(data);

  const { search } = queryString.parse(location.search)
  const [query, setQuery] = useState(search || '')

  useEffect(() => {
    navigate(query ? `/blog/?search=${query}` : ''); 
  }, [query])

  const { localSearchPages } = useStaticQuery(graphql`
    query {
      localSearchPages {
        index
        store
      }
    }
  `)

  const results = useFlexSearch(
    query,
    localSearchPages.index,
    localSearchPages.store
  )

  const resultData = query ? results : posts;

  return (
    <div className={classes.root}>
      <Section className={classes.pagePaddingTop}>
        <Hero query={[query, setQuery]} />
      </Section>
      <SectionAlternate>
        <Horizontal data={resultData} />
      </SectionAlternate>
    </div>
  );
};

export default P2PBlogReachView;
